    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    margin-top: 2em ;
}
.work-exp-top{
    display: flex;
    justify-content: space-between;
    padding: 1em ;
    border-bottom: 1px solid rgb(179, 177, 177);
}
.work-exp-bottom{
    padding: 1em 2em;
}

/* media query */
@media (max-width:765px) {
    .profile-info {
        flex-direction: column;
    }
    .cover-image{
        max-height: 200px;
    }
}
.btn-container{
    display: flex;
}
.add-event-container{
    width: 95%;
    max-width: 600px;
    margin-top: 2em;
    margin-inline: auto;
    border: 1px solid #ccc;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    padding: 2vw;
    border-radius: 5px;
}
.addEvent{
    /* border: 2px solid red; */
}
.addEvent > div {
    margin-bottom: 0.5em;
}
.addEvent > div > *{
    display: block;
}
.addEvent > div > label {
    font-size: 0.9rem;
}
.addEvent input, .addEvent textarea{
    display: inline-block;
    padding: 0.8em 0.8em;
    width: 100%;
    border: none;
    border-radius: 5px;
    font-family: 'Poppins', sans-serif;
    transition: -webkit-transform 150ms linear;
    transition: transform 150ms linear;
    transition: transform 150ms linear, -webkit-transform 150ms linear;
    box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
    font-weight: 400;
    background: inherit;
}
.addEvent textarea  {
    height: -webkit-max-content;
    height: max-content;
    font-size: 1rem;
    resize: none;
}
.file-container > label{
    display: flex;
    align-items: center;
}
.file{
    cursor: pointer;
    /* border: 2px solid red; */
    width: -webkit-max-content;
    width: max-content;
    padding: 0.4em;
    border-radius: 5px;
    background: var(--primary-blue-200);
}
.post-btn{
    margin: auto;
    width: 30%;
    text-align: center;
    font-size: 1rem;
    display: flex;
    justify-content: center;
}
.shareImageContainer{
    position: relative;
    max-width: 300px;
    max-height: 200px;
    margin: auto;
}
.shareImage{
    display: block;
    object-fit: contain;
    width: 100%;
}
.shareCancelImage{
    position: absolute;
    top: -10px;
    right: -10px;
    border: 2px solid #000;
    border-radius: 100vw;
    background-color: #fff;
    color: #000;
}
.events{
    width: 90%;
    max-width: 1240px;
    margin: 2em auto;
    display: flex;
    grid-gap: 0 1em;
    gap: 0 1em;
    -webkit-animation: appear 300ms ease-in-out;
            animation: appear 300ms ease-in-out;
}
.events a{
    text-decoration: none;
    color: var(--primary-clr);
}
.events img{
    width: 100%;
}
.event-categories-container,.events-container,.events{
    /* border: 2px solid green; */
}

.event-categories-container{
    flex: 1.5 1;
}
.events-container{
    flex: 3 1;
}
.add-event-btn{
    padding: 0.8em 1.7em;
    margin: auto;
    font-size: 0.9rem !important;
    align-items: center;
    justify-content: center;
    width: 40%;
}
.post-event{
    /* height: 20vh;
    border: 1px solid #ddd;
    width: 100%; */
}

@media (max-width: 840px) {
    .events{
        flex-direction: column;
    }
}

@media (max-width: 600px) {
    /* html{
        font-size: 15px;
    } */
    .events{
        width: 95%;
    }
}
.event{
    /* min-height: 30vh; */
    /* display: flex; */
    padding: 1em;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    border: 1px solid #bfc7cc;
    margin: 1.5em auto;
    background: var(--primary-bg);
    border-radius: 5px;
    -webkit-animation: appear 300ms ease;
            animation: appear 300ms ease;
}
/**Event top**/
.event-top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 0.5em ;
}
.event-top-left,.event-top-right{
    display: flex;
    align-items: center;
}
.event-top-left{
    display: flex;
    align-items: center;
}
.post-date{
    font-size: 0.8rem;
}
.event-profile-image{
    width: 32px !important;
    aspect-ratio: 1;
    border-radius: 50%;
    margin-right: 0.5em;
    object-fit: cover;
    min-width: 32px;
}
.event-username{
    display: flex;
    align-items: center;
    display: inline-block;
}
.event-username:hover{
    text-decoration: underline;
}
.user-type{
    font-size: 0.8rem;
    color: #555;
    margin-left: 0.5em;
}
/**Event bottom**/
.event-bottom{
    display: flex;
    grid-gap: 0 1em;
    gap: 0 1em;
}
.event-image-container{
    flex: 0.8 1;
    display: flex;
    align-items: center;
    /* border: 2px  solid red; */
}
.event-image{
    display: block;
    width: 100%;
    object-fit: cover;
    max-height: 35vh;
    background: var(--primary-grey-200);
    /* min-height: 30vh; */
    /* border: 2px  solid red; */
}
.event-info{
    flex: 1 1;
    /* border: 2px solid red; */
    /* display: flex; */
    /* align-items: center; */
}
.event-title{
    display: block;
    font-weight: 600;
    font-size: 1.05rem;
    margin-bottom: 0.5em;
    color: var(--primary-clr) !important;
}
.event-title:hover{
    -webkit-text-decoration: underline 1.5px;
            text-decoration: underline 1.5px;
}
.event-description{
    font-size: 0.85rem;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    display: -webkit-box;
    -webkit-box-orient: vertical;
}
.event-date,.event-location{
    display: flex;
    align-items: center;
    margin-top: 0.8em;
    font-weight: 400;
    font-size: 0.8rem;
    color: #444;
}
.event-popover{
    border: 1px solid var(--primary-clr);
    border-radius: 0;
}
.event-popover li{
    width: 100%;
    /* border-bottom: 1px solid var(--primary-grey); */
    border-radius: 0;
    display: flex;
    align-items: center;
    cursor: pointer;
    /* background-color: var(--primary-grey-100); */
    padding: 0.2em 1.2em ;
    color: var(--primary-clr-200);
}
.event-popover li:hover, .event-popover li:active, .event-popover li:focus {
    color: var(--primary-clr);
    background-color: var(--primary-grey-100);
    /* outline: 2px solid var(--primary-btn-bg); */
}
.view-btn{
    /* border: 2px solid black; */
    margin-top: 1.5em;
    margin-left: auto;
    display: block;
    font-family: 'Poppins', sans-serif;
}
@media (max-width: 600px) {
    .event-bottom{
        flex-direction: column-reverse;
        grid-gap: 0.5em 0;
        gap: 0.5em 0;
    }
    .event-details{
        font-size: 0.8rem;
    }
    .event-title{
        font-size: 0.95rem;
    }
}
